class IndexService {
	constructor() {
		if (!wx.cloud) {
			console.error(" 2.2.3 ");
		} else {
			wx.cloud.init({
				traceUser: true,
				env: "pre-release-wzzz1"
			});
		}
		this.db = wx.cloud.database();
		this.task = this.db.collection("task");
	}
	// 
	async addTask(data) {
		let [err, _id] = await this.task.add({data}).then(res => [null, res._id]).catch(err => [err, ""]);
		if (err) {
			uni.showToast({
				title: "",
				icon: "none"
			});
		} else {
			uni.showToast({
				title: ""
			});
			return _id;
		}
	}
	removeTask(id) {
		this.task.doc(id).remove({
			success: function() {
				uni.showToast({
					title: "",
					icon: "none"
				});
			},
			fair: function() {
				uni.showToast({
					title: "",
					icon: "none"
				});
			}
		});
	}
	// 
	async getCurrDate() {
		let res = await wx.cloud.callFunction({
			name: "getServerDate"
		}).then(data => {
			let {time, year, month, date} = data.result;
			return {
				time, year, month, date
			};
		}).catch(err => {
			console.error('[] [login] ', err);
		});
		
		return res;
	}
	// 
	async updateTaskTime(task, duration, time=null) {
		let res;
		if (time) {
			res = time;
		} else {
			await this.getCurrDate().then(data => {
				res = data.time;
			});
		}

		let	arr = task.arr,
			flag = 1;
		
		for (let i = arr.length - 1; i >= 0; i--) {
			let d1 = (new Date(arr[i].date)).getDate(),
				d2 = (new Date(res)).getDate();
			if (d1 == d2) {
				arr[i].count += duration;
				flag = 0;
				break;
			}
		}
		if (flag) {
			arr[arr.length] = {
				date: res,
				count: duration
			};
		}
		this.task.doc(task._id).update({
			data: {
				arr
			}
		}).then(() => {
			uni.showToast({
				title: ""
			});
		});	
	}
	async getAllTask() {
		let res = await this.task.get();
		return res;
	}
	async getOpenid() {
		let _openid = await new Promise((resolve, reject) => {
			wx.cloud.callFunction({
			  name: 'login',
			  success: res => {
				resolve(res.result.openid);
			  },
			  fail: err => {
				console.error('[] [login] ', err);
				reject(err);
			  }
			});
		});
		return _openid;
	}
}

export default new IndexService();